@font-face {
  font-family: "Cera Pro";
  src: url("./fonts/CeraPro-Regular.woff");
  src: local("Cera Pro Regular"),
    local("CeraPro-Regular"),
    url("./fonts/CeraPro-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Cera Pro";
  src: url("./fonts/CeraPro-Medium.woff");
  src: local("Cera Pro Medium"),
    local("CeraPro-Medium"),
    url("./fonts/CeraPro-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Cera Pro";
  src: url("./fonts/CeraPro-Bold.woff");
  src: local("Cera Pro Bold"),
    local("CeraPro-Bold"),
    url("./fonts/CeraPro-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
